import wPicker from "@/components/w-picker/w-picker.vue";

// 添加地址接口:
import {addAddress} from '@/network/getProfileData'

// 导入token：
import { mapGetters } from 'vuex'
export default {
	components:{
		wPicker
	},
	data() {
		return {
			value: [0, 0, 0],
			iconHidden: true,
			showPickerStatus: false,
			searchKey: "",
			real_name:'', //收货人姓名
			is_default:1,//1默认，0不默认
			phone:'',//收货人电话
			detail:'',//收货人详细地址
			resultInfo:'',//三级联动
			id:'',//地址id，默认为空
			post_code:'' ,//邮编
			addressArr:[],//发送的地址格式
			linkList:[]//三级联动数据
		}
	},
	onNavigationBarButtonTap(){
		this.saveClick()
	},
	onBackPress() {
		
	},	
	onLoad(option){
		if(Object.keys(this.$store.state.address).length){
			const obj = this.$store.state.address
			this.detail = obj.detail
			this.phone = obj.phone
			this.id = obj.id
			this.is_default = obj.is_default
			this.real_name = obj.real_name
		}
	},
	methods: {
		//打开三级联动
		openonConfirmClick(){
			this.$refs.picker.show();
		},
		//三级联动
		onConfirm(val){
			this.addressArr = val.checkArr;
			this.resultInfo = val.result;
			console.log(this.addressArr)
		},
		// 保存按钮
		saveClick(){
			if(!this.real_name){
				uni.showToast({
					title:'请输入收货人姓名',
					icon:'none'
				})
			}else if(!(/^1[3456789]\d{9}$/.test(this.phone))){
				uni.showToast({
					title:'请输入正确的手机号',
					icon:'none'
				})
			}else if (!this.detail){
				uni.showToast({
					title:'请输入详细地址',
					icon:'none'
				})
			}else if(!this.post_code){
				uni.showToast({
					title:'请输入邮编',
					icon:'none'
				})
			}else if(!this.resultInfo){
				uni.showToast({
					title:'请输入地址',
					icon:'none'
				})
			}
			else{
				let data = {
					is_default:this.is_default,
					real_name:this.real_name,
					phone:this.phone,
					detail:this.detail,
					post_code:this.post_code,
					id:this.id,
					address:this.addressArr,
				}
				this.addAddress(data)
			}
		},
		addAddress(data){
			uni.showModal({
				title:'是否把此地址作为默认发货地址 ',
				cancelColor:"#333333",
				confirmColor:"#333333",
				confirmText:"立即设置",
				cancelText:'暂不设置',
				success:(res)=> {
					if(res.confirm){
						data.is_default = 1
						
					}else{
						data.is_default = 0
					}
					if(this.isToken){
						addAddress(data,this.isToken).then(res => {
							if(res.data.code == 200){
								uni.showToast({
									title:'保存成功',
									icon:"none",
									success() {
										
									}
								}),
								setTimeout(()=> {
									uni.navigateBack()
								},1500)
								
							}else{
								uni.showToast({
									title:'未知错误',
									icon:"none"
								})
							}
						})
					}else{
						uni.navigateTo({
							url:"../../login/login"
						})
					}
				}
			})
			
		}
	},
	computed:{
		...mapGetters(['isToken'])
	}
}
